.main{
  padding: 207px 0 80px;
  background-image: url("../img/main-bg.svg"), linear-gradient(180deg, #F4F5F9 19.27%, #E3E5EF 102.72%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: -118px;
  &__inner{
    display: grid;
    grid-template-columns: 795px calc(100% - 825px);
    gap: 30px;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 43px;
    line-height: 150%;
    max-width: 490px;
  }
  &__buttons{
    margin-top: 49px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 45px;
    &:not(:last-child){
      margin-right: 16px;
    }
    svg{
      margin-left: 20px;
    }
  }

  .swiper{


  }
  &__slider{
    &-bottom{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 63px;
    }
    &-pagination{
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 16px;
    }
    &-nav{
      display: flex;
      align-items: center;
      margin-left: 48px;
    }
  }
}

.main-slide{
  .swiper{
    &-container{
      overflow: hidden;
    }
    @media screen and (min-width: 1200px){
      &-pagination{
        position: static;
        margin: 0 15px;
        width: 132px;
        height: 3px;
        background: #D7DAE6;
        border-radius: 4px;
        .swiper-pagination-progressbar-fill{
          background-color: $primary !important;
        }
      }
    }

    &-slide{
      img{
        width: 100%;
      }
    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin:0 0 0 10px;
      }
    }
  }
}

.trust{
  padding: 63px 0 88px;
  &__slider{
    margin-top: 83px;
    display: grid;
    grid-template-columns: 60px calc(100% - 200px) 60px;
    gap: 40px;
    align-items: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-slide{


    }
    &-pagination{
      display: none;
    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin: 0;
      }
    }
  }
}

.block{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.categories{
  padding: 85px 0 100px;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - ((46px * 3) / 4)));
    gap: 35px 46px;
  }
}

.use{
  padding: 100px 0 116px;
  background: $blockBg;
  margin-top: 23px;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(3, calc((100% / 3) - ((46px * 2) / 3)));
    gap: 35px 46px;
  }
}

.consult{
  padding: 150px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 48.75%;
    gap: 30px;
  }
  &__content{
    max-width: 608px;
    display: flex;
    flex-direction: column;
  }
  &__desc{
    padding-top: 40px;
    max-width: 400px;
    margin-top: auto;
    &--mt40{
      margin-top: 0;
    }
  }
  &__form{
    &-grid{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;
    }
  }
  &__full{
    grid-column: 1/-1;
  }
  &__btn{
    margin: 61px 0 0 auto;
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
  &__contacts{
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    max-width: 545px;
    &-title{
      margin-bottom: 13px;
    }
  }
}

.materials{
  padding: 25px 0 140px;
  &--pt112{
    padding-top: 112px;
  }
  &__desc{
    margin-top: 26px;
  }
  .swiper{
    &-container{
      margin-top: 63px;
    }
  }
  .slider-section__nav{
    display: none;
  }
}

.service{
  padding: 100px 0 55px;
  background-color: $blockBg;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    width: 100%;
  }
  &__grid{
    width: 100%;
    margin-top: 70px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - ((38px * 3) / 4)));
    gap: 35px 38px;
  }
  &__icon{
    height: 60px;
    margin-bottom: 25px;
    img{
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 25px;
  }
  &__link{
    margin-top: 65px;
    padding: 0 46px;
    svg{
      margin-left: 25px;
    }
  }
}

.sections-group{
  position: relative;
  &__img{
    position: absolute;
    right: 0;
    bottom: -30px;
    pointer-events: none;
  }
  section{
    position: relative;
    z-index: 2;
  }
}

.news{
  padding: 128px 0 43px;
  &__grid{
    margin-top: 58px;
  }
}

.contacts{
  margin-bottom: 50px;
  padding: 42px 0;
  position: relative;
  z-index: 2;
  &--pt0{
    padding-top: 0;
  }
}

.map{
  height: 618px;
  width: 100%;
}

.contact-card{
  position: absolute;
  background: #FFFFFF;
  right: calc(50% - 775px);
  top: 50%;
  transform: translateY(-50%);
  width: 526px;
  min-height: 100%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  &__tabs{
    display: flex;
    align-items: center;
  }
  .tab{
    flex-grow: 1;
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $blockBg;
    font-weight: 600;
    font-size: 18px;
    font-family: $font2;
    color: $black;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      background-color: rgba($blockBg,.8);
    }
    &--active{
      pointer-events: none;
      color: $primary;
      background-color: #fff;
    }
  }
  &__content{
    padding: 47px 50px 57px;
    flex-direction: column;
    flex-grow: 1;
    &.tab__content--active{
      display: flex;
    }
  }
  &__items{
    margin-top: 40px;
  }
  &__item{
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
  &__icon{
    display: none;
  }
  &__desc{
    margin-top: 13px;
  }
  &__nav{
    margin-top: auto;
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 26px 0 31px;
    svg{
      margin-left: 11px;
    }
  }
}


@media screen and (max-width: 1610px){
  .contact-card{
    right: 30px;
  }
}


@media screen and (max-width: 1450px){
  .main{
    &__inner{
      grid-template-columns: 670px calc(100% - 680px);
      gap: 10px;
    }
  }

  .categories,
  .service{
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - ((30px * 2)/ 3)));
      gap: 30px;
    }
  }

  .use{
    &__grid{
      grid-template-columns: repeat(2, calc(50% - 15px));
      gap: 30px;
    }
  }

  .materials{
    .slider-section__nav{
      display: flex;
      align-items: center;
      margin-top: 35px;
    }
  }
}


@media screen and (max-width: 1200px){
  .main{
    margin-top: -98px;
    padding: 120px 0 110px;
    &__inner{
      grid-template-columns: 100%;
      gap: 65px;
    }
    &__content{
      max-width: 670px;
    }
    &__slider{
      max-width: 500px;
      &-current,
      &-all{
        display: none;
      }
      &-pagination{
        width: 100%;
      }
      &-bottom{
        position: relative;
        display: block;
      }
    }
    &__desc{
      margin-top: 15px;
    }
    &__buttons{
      margin-top: 20px;
      flex-wrap: wrap;
    }
    &__btn{
      margin-top: 9px;
      padding: 0 40px;
      svg{
        margin-left: 15px;
        width: 14px;
        height: auto;
      }
    }
  }

  .main-slide{
    .swiper{
      &-pagination{
        position: static;
      }
      &-button{
        &-prev,
        &-next{
          position: absolute;
          top: 0;
          margin: 0;
        }
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }

  .trust{
    padding: 32px 0 42px;
    .swiper{
      &-container{
        order: -1;
        grid-column: 1/-1;
      }
      &-pagination{
        display: flex;
        position: static;
        padding: 0;
      }
      &-button{
        &-next{
          order: 4;
        }
      }
    }
    &__slider{
      gap: 75px 0;
      grid-template-columns: 52px calc(100% - 104px) 52px;
    }
  }

  .categories{
    padding: 42px 0 32px;
  }

  .use{
    padding: 47px 0 76px;
  }

  .consult{
    padding: 65px 0;
  }

  .materials{
    padding: 60px 0;
    .swiper-container{
      margin-top: 44px;
    }
  }

  .service{
    padding: 90px 0 77px;
  }

  .news{
    padding: 67px 0 27px;
    &__grid{
      margin-top: 22px;
    }
  }

  .contacts{
    padding: 15px 0 0;
  }

  .contact-card{
    background-color: transparent;
    width: 100%;
    position: static;
    transform: unset;
    &__content{
      padding: 40px 30px 50px;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__title{
      span{
        display: none;
      }
    }
    &__desc{
      line-height: 133%;
    }
  }

  .trust{
    overflow: hidden;
    .swiper{
      &-slide{
        width: 153px;
      }
      &-container{
        overflow: unset;
      }
    }
  }

  .block{
    &__top{
      display: grid;
      grid-template-columns: 100%;
      gap: 18px;
    }
  }

  .categories,
  .use,
  .service{
    &__grid{
      margin-top: 23px;
      grid-template-columns: repeat(2, calc(50% - 7.5px));
      gap: 15px;
    }
  }

  .consult{
    &__inner{
      grid-template-columns: 100%;
      gap: 70px;
    }
    &__desc{
      padding-top: 20px;
    }
    &__form{
      &-grid{
        gap: 30px;
      }
    }
    &__btn{
      margin: 54px 0 0;
      padding: 0 40px;
    }
    &__contacts{
      padding-top: 24px;
    }
  }

  .service{
    &__grid{
      margin-top: 60px;
    }
  }

  .contact-card{
    &__content{
      padding-left: 15px;
      padding-right: 15px;
    }
    &__title{
      display: none;
    }
    &__items{
      margin: 0;
    }
    &__item{
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    &__desc{
      margin-top: 16px;
    }
    &__nav{
      padding-top: 30px;
    }
    &__subtitle{
      display: flex;
    }
    &__icon{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width: 20px;
      height: 26px;
      margin-right: 13px;
    }
  }

  .map{
    height: 565px;
  }
}



@media screen and (max-width: 800px){
  .categories,
  .use{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 768px){
  .consult{
    &__form{
      &-grid{
        grid-template-columns: 100%;
      }
    }
  }

  .service{
    &__grid{
      grid-template-columns: 100%;
      gap: 60px;
      text-align: center;
      padding: 0 40px;
    }
    &__btn{
      margin-top: 60px;
      padding: 0 40px;
    }
  }
}
