.error {
    padding: 0 0 220px;
    background-image: url(../img/error-bg.png);
    background-repeat: no-repeat;
    background-position: top 0 left 0;
    background-size: 100% auto;
    &__inner {
        display: flex;
        justify-content: space-between;
    }
    &__left {
        max-width: 630px;
    }
    &__right {
        font-weight: 500;
        font-size: 516px;
        line-height: 110%;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        font-feature-settings: 'ss07' on, 'ss08' on;
        font-family: $font2;
        font-feature-settings: 'ss07' on, 'ss08' on;
        background: linear-gradient(96.39deg, #0D899F -11.35%, #19B8AE 114.56%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        text-shadow: 0px 0px #00000000;
    }
    &__subtitle {
        margin-top: 40px;
    }
    &__items {
        margin-top: 106px;
    }
    &__item {
        display: flex;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 34px;
        }
        .social-btn{
            margin-right: 20px;
        }
    }
    &__social-network {
        margin-top: 22px;
        display: flex;
        align-items: center;
        padding-left: 71px;
    }
    &__links {
        display: flex;
        align-items: center;
        margin-left: 19px;
    }
    &__link {
        width: 52px;
        height: 52px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        &:not(:first-child) {
            margin-left: 12px;
        }
    }
    &__telegram {
        background: #50ABFF;
    }
    &__whatsapp {
        background: #4CB663;
    }
}
