.trust-card{
  position: relative;
  padding-bottom: 100%;
  filter: grayscale(1);
  border-radius: 50%;
  img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    max-width: calc(100% - 40px);
    max-height: calc(100% - 40px);
  }
  &:hover{
    filter: grayscale(0);
    background: #D4EBF1;
  }
}

.category-card{
  background: #D4EBF1;
  padding: 9px 37px 28px;
  position: relative;
  &.color2{
    background: $blockBg;
    .category-card__link{
      background: $blockBg;
      path{
        stroke: $black;
      }
      &:hover{
        background: rgba($blockBg,20%);
        opacity: 1;
      }
    }
  }
  &__img{
    position: relative;
    padding-bottom: 65%;
    display: block;
    margin-bottom: 9px;
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 25px;
  }
  &__link{
    width: 80px;
    height: 80px;
    border: 10px solid $pageBg;
    border-radius: 50%;
    position: absolute;
    bottom: -20px;
    right: -20px;
  }
}

.use-card{
  background-color: #fff;
  padding: 41px 20px 35px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__img{
    height: 151px;
    display: flex;
    align-items: center;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
  &__title{
    margin-top: 17px;
  }
  &__num{
    font-weight: 500;
    font-size: 245px;
    font-family: $font2;
    opacity: 0.07;
    font-variant-numeric: tabular-nums;
    -webkit-font-feature-settings: "tnum";
    font-feature-settings: "tnum";
  }
}

.material-card{
  display: flex;
  flex-direction: column;
  &__num{
    color: $primary;
    text-align: right;
  }
  &__title{
    margin: 17px 0 35px;
  }
  &__desc{
    margin: 5px 0 55px;
  }
  &__img{
    margin-top: auto;
    position: relative;
    padding-bottom: 89.2%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.news-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &--search{
    justify-content: unset;
    .news-card__img{
      margin-right: 87px;
    }
  }
  &:not(:last-child){
    padding-bottom: 35px;
    margin-bottom: 35px;
    border-bottom: 1px solid $blockBg;
  }
  &__img{
    flex-shrink: 0;
    width: 23.4%;
    display: block;
    margin-right: 15px;
    img{
      width: 100%;
    }
  }
  &__content{
    flex-grow: 1;
    max-width: 760px;
    width: 100%;
  }
  &__day,
  &__month{
    letter-spacing: 0.05em;
  }
  &__month{
    font-family: $font2;
    font-size: 20px;
    line-height: 23px;
    margin-top: 7px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: 9px;
  }
}

.video-card{
  &__media{
    padding-bottom: 60%;
    position: relative;
    display: block;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    margin-top: 20px;
  }
  &__desc{
    margin-top: 15px;
  }
}

.team-card{
  background: $blueBg;
  &--two {
    background: $blockBg;
  }
  &__img {
    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
  &__desc {
    padding: 30px 35px 45px;
  }
  &__text {
    margin-top: 35px;
    span {
      font-size: 18px;
      line-height: 24px;
      color: $gray;
      display: block;
      &:not(:first-child) {
        margin-top: 9px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .news-card{
    display: grid;
    gap: 36px 31px;
    grid-template-columns: 320px calc(100% - 361px);
    &--search{
      grid-template-columns: 100% !important;
      gap: 20px;
      .news-card__img{
        height: unset;
      }
    }
    &__content{
      grid-column: 1/-1;
    }
    &__img{
      width: 100%;
      margin: 0;
      max-width: 500px;
    }
    &__desc{
      margin-top: 20px;
    }
  }
}


@media screen and (max-width: 992px){
  .category-card{
    display: flex;
    align-items: center;
    padding: 5px 20px 5px 2px;
    min-height: 110px;
    max-width: calc(100% - 15px);
    &__img{
      position: static;
      padding: 0;
      width: 102px;
      height: 81px;
      flex-shrink: 0;
      margin-right: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        transform: unset;
        position: static;
      }
    }
    &__desc{
      display: none;
    }
    &__link{
      width: 60px;
      height: 60px;
      border-width: 8px;
      bottom: -8px;
      right: -30px;
    }
  }

  .use-card{
    display: block;
    padding: 5px 20px 5px 2px;
    max-width: calc(100% - 15px);
    position: relative;
    &__num{
      display: none;
    }
    &__content{
      display: flex;
      align-items: center;
      height: 100%;
      min-height: 110px;
    }
    &__img{
      position: static;
      padding: 0;
      width: 102px;
      height: 81px;
      flex-shrink: 0;
      margin-right: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        transform: unset;
        position: static;
      }
    }
    &__title{
      margin: 0;
    }
    &:before{
      content: '';
      position: absolute;
      background-color: #FFFFFF;
      background-image: url(../img/arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      border: 8px solid $blockBg;
      width: 60px;
      height: 60px;
      bottom: -8px;
      right: -30px;
      border-radius: 50%;
    }
  }

  .material-card{
    &__top{
      margin-bottom: 35px;
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
    }
    &__title{
      margin: 0 10px 0 0;
    }
  }
}


@media screen and (max-width: 768px){
  .news-card{
    grid-template-columns: 190px calc(100% - 221px);
    &__month{
      font-size: 15px;
      line-height: 173%;
      margin: 0;
    }
    &__img{
      height: 120px;
      img{
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
