.company-desc {
    padding: 0 0 56px;
    &__inner {
        display: flex;
    }
    &__left {
        position: relative;
        z-index: 2;
        width: 1070px;
    }
    &__right {
        width: calc(100% - 1070px);
    }
    &__desc {
        background: #FFFFFF;
        padding: 40px 40px 30px;
        margin-top: 35px;
        p {
            font-size: 18px;
            line-height: 24px;
            color: $gray;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        ul {
            margin-bottom: 20px;
            li {
                font-size: 18px;
                line-height: 24px;
                color: $gray;
            }
        }
    }
    &__img {
        position: relative;
        margin-top: 0;
        img {
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}

.competencies {
    padding: 50px 0 60px;
    overflow: hidden;
    &__swiper {}
    &__inner {
        display: flex;
        align-items: center;
    }
    &__left {
        position: relative;
        z-index: 3;
        width: calc(100% - 48.7% - 60px);
    }
    &__right {
        width: 48.7%;
        margin-left: 60px;
        position: relative;
        padding: 50px 30px 60px;
        &::after,
        &:before{
            content: '';
            position: absolute;
            top: 0;
            width: 5000px;
            height: 100%;
        }
        &:after{
            background-color: #FFFFFF;
            left: 0;
        }
        &:before{
            background-color: $pageBg;
            right: 100%;
            z-index: 2;
        }

    }
    &__slide {
        position: relative;
    }
    &__slide-nav {
        display: flex;
        justify-content: end;
        align-items: center;
        margin-bottom: 70px;
    }
    &__button {
        &-prev {
            position: static;
        }
        &-next {
            position: static;
            margin-left: 10px;
        }
    }
    &__desc {
        margin-top: 45px;
    }
    &__slider{
        position: relative;

    }
    .swiper{
        overflow: unset;
        width: 312px;
        max-width: 100%;
        margin-left: 0;
        position: relative;
        &:before{
            z-index: 2;
            content: '';
            position: absolute;
            height: 1px;
            width: 2000px;
            top: 10px;
            left: 0;
            background: rgba(102, 105, 115, 0.14);
        }
        &-slide{
            &-active{
                .competency-card__round{
                    background: linear-gradient(96.39deg, #0D899F -11.35%, #19B8AE 114.56%);
                }
            }
        }
    }
}

.competency-card{
    &__subtitle {
        margin-top: 25px;
    }
    &__desc {
        margin-top: 30px;
    }
    &__round{
        width: 21px;
        height: 21px;
        border-radius: 50%;
        background-color: $black;
        display: block;
        margin-bottom: 38px;
    }
}

.team {
    padding: 60px 0 90px;
    &__slide {
        position: relative;
        &-nav {
            margin-bottom: 70px;
        }
    }
    .swiper {
        width: 100%;
        height: 100%;
    }
    &__slide-nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__nav-right {
        display: flex;
        align-items: center;
    }
    &__button {
        &-next {
            position: static;
            margin-left: 10px;
            width: 52px;
            height: 52px;
        }
        &-prev {
            position: static;
            width: 52px;
            height: 52px;
        }
    }
}



.advantages {
    background: #E3E5EF;
    padding: 90px 0;
    margin: 90px 0 65px;
    &__slide {
        position: relative;
    }
    .swiper {
        width: 100%;
        height: 100%;
    }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 70px;
        &-right {
            display: flex;
            align-items: center;
        }
    }
    &__button {
        &-prev,
        &-next {
            position: static;
        }
        &-next {
            margin-left: 10px;
        }
    }
    &__card {
        background: rgba(255, 255, 255, 0.6);
        text-align: center;
        padding: 50px 15px 35px;
        height: 387px;
        display: flex;
        flex-direction: column;
        &-img {
            margin-bottom: 50px;
        }
        &-desc {
            font-weight: 600;
            font-size: 19px;
            line-height: 28px;
            margin-top: auto;
        }
    }
}

.principles {
    padding: 65px 0 200px;
    &__items {
        margin-top: 55px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 38px;
    }
    &__item {
        &-img {
            &--two {
                margin-bottom: 70px;
            }
            &--three {
                margin-top: 45px;
            }
            img {
                width: 100%;
                height: auto;
            }
        }
        &-subtitle {
            color: #000000;
        }
        &-text {
            margin-top: 38px;
        }
    }
}
