.site-map{
  padding-bottom: 130px;
  &__inner{
    padding: 53px 80px 65px;
    margin-top: 55px;
    background: #FFFFFF;
    display: grid;
    grid-template-columns: 1.4fr 1.3fr 1fr;
    gap: 50px;
  }
}

.site-group{
  &:not(:last-child){
    margin-bottom: 11px;
  }
  &__title{
    font-weight: 600;
    font-size: 19px;
    line-height: 165%;
    text-decoration-line: underline;
    font-family: $font2;
  }
  &__links{
    margin-top: 21px;
    display: flex;
    flex-direction: column;
    max-width: 385px;
  }
  &__link,
  &__sublink2,
  &__sublink3{
    margin-right: auto;
    font-size: 18px;
    line-height: 133%;
    &::before{
      content: '';
      display: inline-block;
      width: 3px;
      height: 3px;
      border-radius: 50%;
      background-color: $gray;
      transform: translateY(-5px);
      margin: 0 12px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 13px;
    }
  }
  &__subgroup2{
    margin-top: 13px;
    padding-left: 40px;
    display: flex;
    flex-direction: column;
  }
  &__sublink2,
  &__sublink3{
    font-size: 16px;
    line-height: 150%;
    color: #646570;
    &:before{
      transform: translateY(-4px);
    }
  }
  &__subgroup3{
    padding-left: 22px;
  }
}
