.main-partners{
  padding-bottom: 85px;
  &--delivery{
    .main-partners__desc{
      max-width: 425px;
    }
  }
  .breadcrumbs{
    padding-bottom: 10px;
  }
  &__inner{
    display: grid;
    grid-template-columns: 540px calc(100% - 540px);
    align-items: center;
  }
  &__img{
    margin-left: -315px;
    width: calc(100% + 315px);
    position: relative;
    img{
      z-index: 2;
      position: relative;
      width: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 4;
    padding: 43px 79px 43px 0;
    margin-top: -11px;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 2000px;
      background-color: $blueBg;
    }
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 40px;
  }
  &__link{
    position: absolute;
    bottom: -30px;
    right: -30px;
    width: 160px;
    height: 120px;
    background-color: $blueBg;
    z-index: 3;
    &:hover{
      background-color: darken($blueBg,10%);
    }
  }
  &__rect{
    position: absolute;
    bottom: -30px;
    left: -30px;
    width: 100px;
    height: 100px;
    border: 4px solid $blueBg;
  }
}

.partners{
  padding: 85px 0 45px;
  overflow: hidden;
  &__slider{
    background-color: #fff;
  }
  .swiper{
    &-slide{
      width: auto;
      height: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-button{
      &-prev,
      &-next{
        top: 0;
        margin-top: 0;
      }
    }
  }
  &__info{
    margin-top: 54px;
    position: relative;
  }
  &__content{
    display: none;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &--active{
      display: flex;
    }
  }
  &__desc{
    margin-top: 60px;
    max-width: 1100px;
  }
  &__link{
    margin: 48px auto 0;
    padding: 0 46px;
    svg{
      margin-left: 16px;
    }
  }
}

.delivery-info{
  margin-top: 57px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
  &__num{
    font-weight: 500;
    font-size: 90px;
    line-height: 148%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #13909A;
    font-family: $font2;
  }
  &__desc{
    margin-top: 23px;
  }
}
