@import "../mixins";
.logo{
  display: block;
  width: 169px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 25px 0;
  position: relative;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    margin-right: 44px;
  }
  &__right{
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__contacts{
    text-align: right;
    margin-left: auto;
    padding-left: 20px;
    flex-shrink: 0;
    &--mob{
      display: none;
    }
  }
  &__phone{
    font-weight: 700;
    font-size: 22px;
    line-height: 150%;
    font-family: $font2;
    color: $primary;
    display: block;
    &:hover{
      color: $black;
    }
  }
  &__mail{
    font-weight: 700;
    font-size: 18px;
  }
  &__search{
    margin-left: 30px;
    width: 191px;
    input{
      width: 100%;
      height: 52px;
      padding: 0 10px 0 57px;
      background-color: $bg;
      border-radius: 1000px;
      border: none;
      background-image: url(../img/search.svg);
      background-repeat: no-repeat;
      background-position: left 20px center;
    }
    &-toggle,
    &-close{
      display: none;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-left: 10px;
    &--mob{
      display: none;
    }
  }
  &__social,
  &__lang{
    font-weight: 700;
    font-size: 18px;
    font-family: $font2;
    color: $primary;
  }
  &__call,
  &__burger{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  &__item{
    text-align: center;
    font-size: 18px;
    line-height: 150%;
    position: relative;
    &:not(:last-child){
      margin-right: 24px;
    }

    &-show{
      display: flex;
      align-items: center;
      line-height: inherit;
      transition: all .3s;
      svg{
        margin-left: 13px;
      }
      &:hover{
        color: $primary;
        cursor: pointer;
        path{
          stroke: $primary;
        }
      }
    }
    &-hidden{
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      padding-top: 44px;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &-inner{
        filter: drop-shadow(0px 4px 36px rgba(0, 0, 0, 0.1));
        background: #F7F8FA;
      }
    }
    &-link{
      min-width: 172px;
      padding: 0 30px;
      height: 57px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        border-bottom: 1px solid #E3E5EF;
      }
    }
    &--active{
      .nav__item-show{
        color: $primary;
        path{
          stroke: $primary;
        }
        svg{
          transform: rotate(180deg);
        }
      }
      .nav__item-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &--catalog{
      @media screen and (min-width: 1200px){
        .nav__item-hidden{
          position: fixed;
          left: 0;
          width: 100vw;
          top: 40px;
          height: calc(100vh - 40px);
          transform: unset;
          padding-top: 78px;
        }
      }
    }
  }
}

.header-catalog{
  background-color: #fff;
  z-index: 10;
  height: 100%;
  width: 100%;
  text-align: left;
  padding: 30px 0 50px;
  &__scroll{
    width: calc(100% - 24px);
    margin: 0 auto;
    height: 100%;
    overflow-y: auto;
    @include scroll;
  }
  .container{
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__inner{
    flex-grow: 1;
    column-count: 4;
    column-gap: 45px;
  }
  &__group{
    display: inline-block;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  &__title,
  &__link{
    font-size: 18px;
    line-height: 150%;
  }
  &__title{
    font-weight: 700;
    margin-bottom: 25px;
    svg{
      display: none;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    margin-right: auto;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
}


@media screen and (max-width: 1600px){
  .header{
    &__search{
      width: unset;
      margin-left: auto;
      padding-left: 20px;
      &-wrap{
        width: 100vw;
        overflow: hidden;
        position: fixed;
        top: -100%;
        left: 0;
        z-index: 101;
        background: linear-gradient(180deg, #FFFFFF -22.81%, #E3E5EF 102.72%);
        height: 70px;
        display: flex;
        align-items: center;
        padding: 0 15px;
        transition: all .2s;
      }
      &-toggle{
        display: unset;
      }
      &-bg{
        z-index: 100;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: rgba($black,.6);
        display: none;
      }
      input{
        height: 100%;
        border-radius: 0;
        background-color: transparent;
        background-size: 34px auto;
        padding: 0 60px;
        background-position: left center;
        font-size: 18px;
      }
      &-close{
        position: absolute;
        display: flex;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
      }
      &--active{
        .header__search-wrap{
          top: 0;
        }
        .header__search-bg{
          display: block;
        }
      }
    }
    &__socials{
      margin-left: auto;
      padding-left: 20px;
    }
  }
}


@media screen and (max-width: 1420px){
  .header{
    &__logo{
      width: 140px;
      margin-right: 20px;
    }
    &__socials,
    &__search,
    &__contacts{
      padding-left: 10px;
    }
  }
}


@media screen and (max-width: 1320px){
  .header{
    &__logo{
      width: 120px;
    }
    &__social,
    &__lang{
      @media screen and (min-width: 1200px){
        width: 40px;
        height: 40px;
        &:not(:last-child){
          margin-right: 5px;
        }
      }
    }
  }

  .nav{
    &__item{
      &:not(:last-child){
        margin-right: 10px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .header{
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      margin: 0 10px;
    }
    &__right{
      flex-grow: unset;
    }
    &__mob{
      padding: 0 0 20px;
      background: #F6F7FA;
      position: fixed;
      top: 98px;
      left: 0;
      width: 100%;
      height: calc(100vh - 98px);
      overflow-y: auto;
      transform: translateX(-120%);
      transition: all .8s;
      &-buttons{
        display: flex;
        align-items: center;
      }
    }
    &__socials,
    &__contacts{
      &--dekstop{
        display: none;
      }
    }
    &__socials{
      margin-top: 28px;
      padding: 0 30px;
      &--mob{
        display: flex;
      }
    }
    &__contacts{
      text-align: left;
      padding: 0 30px;
      &--mob{
        display: block;
      }
    }
    &__phone,
    &__mail{
      font-weight: 400;
      font-size: 15px;
      line-height: 20px;
      font-family: $font1;
      color: $black;
      display: block;
      margin-top: 28px;
    }
    &__call{
      display: flex;
      margin-right: 10px;
    }
    &__search{
      padding-left: 0;
    }
    &__burger{
      width: 32px;
      height: 20px;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 15;
      margin-right: 44px;
      span{
        display: block;
        width: 100%;
        height: 2px;
        background: $black;
        right: 0;
        transition: all .3s;
        border-radius: 2px;
        &:nth-child(1){
          position: absolute;
          top: 0;
          width: 50%;
        }
        &:nth-child(3){
          position: absolute;
          bottom: 0;
        }
      }
    }
    &--active{
      .header{
        &__burger{
          span{
            &:nth-child(1){
              width: 100%;
              transform: rotate(-45deg) translate(-7px,5px);
            }
            &:nth-child(2){
              transform: scale(0);
            }
            &:nth-child(3){
              transform: rotate(45deg) translate(-7px,-5px);
            }
          }
        }
        &__mob{
          transform: translateX(0);
        }
      }
    }
  }

  .nav{
    display: block;
    a.nav__item{
      padding: 0 30px;
      height: 62px;
      display: flex;
      align-items: center;
      width: 100%;
    }
    &__item{
      margin: 0 !important;
      font-size: 15px;
      font-weight: 700;
      text-align: unset;
      br{
        display: none;
      }
      &-show{
        height: 62px;
        padding: 0 30px;
        svg{
          margin-left: 7px;
          transform: rotate(-90deg);
        }
      }
      &-hidden{
        opacity: 1;
        pointer-events: unset;
        position: static;
        transform: unset;
        padding: 0;
        background: $blockBg;
        transition: unset;
        display: none;
        &-inner{
          padding: 0;
          filter: unset;
          background-color: transparent;
        }
      }
      &-link{
        border: none !important;
      }
      &--active{
        .nav__item-show{
          svg{
            transform: rotate(0deg);
          }
        }
      }
    }
  }

  .header-catalog{
    position: static;
    background-color: transparent;
    padding: 0;
    &__scroll{
      width: unset;
      overflow: unset;
    }
    .container{
      padding: 0;
    }
    &__links{
      display: none;
    }
    &__inner{
      column-count: 1;
    }
    &__title,
    &__link{
      font-size: 15px;
      line-height: 22px;
      padding: 20px 30px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 0 !important;
    }
    &__title{
      svg{
        margin-left: 10px;
        display: unset;
      }
      &--active{
        svg{
          transform: rotate(90deg);
        }
      }
    }
    &__group{
      margin: 0 !important;
    }
    &__link{
      font-weight: 400;
      padding-left: 45px;
    }
    &__group,
    &__link{

    }
  }
  
  .nav__item-link,
  .header-catalog__link,
  .header-catalog__group{
    &:not(:last-child){
      position: relative;
      &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #fff;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 14px 0;
    &__logo{
      width: 107px;
    }
    &__mob{
      top: 71px;
      height: calc(100vh - 71px);
    }
  }

  a.nav__item,
  .nav__item-show,
  .header__socials,
  .header__contacts{
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
}
