@import "../vars.scss";
.breadcrumbs {
    padding: 30px 0 70px;
    &__item {
        font-weight: 500;
        font-size: 15px;
        line-height: 160%;
        display: inline;
        color: $gray;
        &:last-child {
            color: $primary;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                color: $gray;
                margin: 0 5px 0 3px;
            }
        }
    }
    &__back{
        display: none;
    }
}


@media screen and (max-width: 1200px){
    .breadcrumbs{
        padding: 2px 0 22px;
        &__back{
            display: flex;
            margin-bottom: 22px;
            svg{
                margin: 0 15px 0 0;
            }
        }
    }
}
