.catalog-page{
  padding-bottom: 72px;
  &__btn{
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
}

.main-category{
  background-image: url("../img/wave4.svg");
  background-repeat: no-repeat;
  background-position: center bottom 54px;
  background-color: #fff;
  margin-top: -118px;
  padding: 118px 0 100px;
  &__inner{
    margin-top: 15px;
    display: grid;
    grid-template-columns: 618px calc(100% - 650px);
    gap: 32px;
  }
  &__content{
    max-width: 618px;
  }
  &__img{
    max-width: 679px;
    margin-left: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__slider{
    max-width: 700px;
    margin-left: auto;
    img{
      display: block;
      max-width: 570px;
    }
  }
  &__title{
    margin-top: 15px;
  }
  &__subtitle2{
    margin-top: 60px;
    max-width: 500px;
    text-transform: uppercase;
    color: $black;
    font-weight: 700;
  }
  &__desc{
    margin-top: 60px;
    max-width: 598px;
  }
  &__nav{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    margin-top: 49px;
    &--flex{
      display: flex;
      gap: unset;
      .main-category__btn{
        padding: 0 46px;
        &:not(:last-child){
          margin-right: 16px;
        }
      }
    }
  }
  &__btn{
    svg{
      margin-left: 20px;
    }
  }
}

.catalog-category{
  padding: 150px 0;
  &--pt80{
    padding-top: 80px;
  }
  .categories__grid{
    margin: 0;
  }
}

.filter{
  display: flex;
  margin-bottom: 48px;
  &__item{
    min-width: 110px;
    &:not(:last-child){
      margin-right: 70px;
    }
  }
}

.service-desc{
  padding: 80px 0;
  &--pt110{
    padding-top: 110px;
  }
  &--pt188{
    padding: 188px 0 4px;
  }
  &__title2{
    margin-bottom: 78px;
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 48.71% - 38px) 48.71%;
    gap: 38px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 2000px;
      height: 100%;
      background-color: #fff;
      right: 401px;
      top: 0;
    }
  }
  &__content{
    padding: 60px 0;
  }
  &__img,
  &__content{
    position: relative;
    z-index: 2;
  }
  &__img{
    margin-top: -78px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(102, 105, 115, 0.27);
  }
  &__text{
    max-width: 754px;
  }
}

.slider-section{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 72px;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-pagination{
      display: none;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev,
      &-next{
        margin: 0 0 0 10px;
        position: static;
      }
    }
  }
}

.collection{
  padding: 10px 0 130px;
  .swiper-container{
    padding: 0 20px 20px 0;
  }
}

.videos{
  padding: 55px 0 110px;
}

.functionality{
  padding: 40px 0 75px;
}

.opportunity{
  padding: 75px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left{
    width: 360px;
  }
  &__title{
    margin-top: 15px;
  }
  &__desc{
    margin-top: 85px;
  }
  &__right{
    width: calc(100% - 390px);
    max-width: 1091px;
    position: relative;
  }
  &__img{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    max-height: 92%;
    width: auto;
    display: block;
  }
  &__content{
    padding: 80px 50px 50px 85px;
    max-width: 635px;
    margin-left: auto;
    background-color: $blueBg;
    position: relative;
    z-index: 2;
  }
  &__desc2{
    margin-top: 28px;
  }
}

.props{
  padding: 75px 0 54px;
  &__grid{
    margin-top: 55px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
    color: $gray;
  }
  &__col{
    &:nth-child(1){
      .props__item{
        padding-left: 30px;
      }
    }
  }
  &__item{
    height: 58px;
    display: flex;
    align-items: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding: 0 15px;
    font-size: 18px;
    line-height: 133%;
    &:nth-child(odd){
      background: #FFFFFF;
    }
  }
  &__bottom{
    margin-top: 46px;
    display: flex;
    justify-content: center;
  }
  &__link{
    padding: 0 46px;
    svg{
      margin-left: 15px;
    }
  }
}


@media screen and (max-width: 1200px){
  .slider-section{
    .container{
      position: relative;
      padding-bottom: 77px;
    }
    &__nav{
      justify-content: space-between;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 15px;
      width: 100%;
    }
    .swiper{
      &-pagination{
        display: flex;
        position: static;
        width: unset;
        padding: 0;
      }
      &-button{
        &-next,
        &-prev{
          flex-shrink: 0;
          margin-left: 0;
        }
      }
    }
  }
}
