.news-page{
  padding-bottom: 30px;
  background-image: url(../img/wave2.svg);
  background-position: center top;
  background-repeat: no-repeat;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 51px;
  }
  &__links{
    display: flex;
    align-items: center;
    padding: 9px 13px;
    background: #FFFFFF;
    border-radius: 1000px;
  }
  &__link{
    padding: 0 24px;
    display: flex;
    align-items: center;
    height: 36px;
    font-weight: 700;
    font-size: 18px;
    line-height: 161%;
    &:not(:last-child){
      border-right: 1px solid #D9D9D9;
    }
    &--active{
      color: $primary;
      pointer-events: none;
    }
  }
  &__grid{
    margin-top: 40px;
  }
}

.search-page{
  padding-bottom: 132px;
  &__grid{
    margin-top: 73px;
  }
}

.news-item{
  padding: 0 0 18px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__date{
    font-size: 20px;
  }
  &__title{
    margin-top: 25px;
    text-transform: unset;
  }
}

.news-social{
  margin-top: 70px;
  &__items{
    margin-top: 26px;
    display: flex;
    align-items: center;
  }
  &__item{
    &:not(:last-child){
      margin-right: 30px;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.news-content{
  margin-top: 47px;
  img{
    width: 100%;
    display: block;
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  h2{
    max-width: 1141px;
    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
  .mlist{
    margin: 40px 0;
    line-height: inherit;
    padding-left: 70px;
    h5{
      margin: 0 !important;
    }
  }
}


@media screen and (max-width: 1200px){
  .search-page{
    padding-bottom: 100px;
    &__grid{
      margin-top: 43px;
    }
  }
}
