.use-page{
  padding-bottom: 6px;
  background-image: url(../img/wave5.svg);
  background-position: center top;
  background-repeat: no-repeat;
  &__subtitle{
    margin-top: 35px;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }
  &__link{
    padding: 0 33px;
    svg{
      margin-left: 16px;
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: 332px calc(100% - 332px);
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
    }
  }
}

.use-info{
  background-color: $blueBg;
  width: 726px;
  padding: 60px;
  position: relative;
  z-index: 2;
  &:before{
    display: none;
  }
  &__top{
    display: flex;
    align-items: flex-end;
  }
  &__img{
    width: 122px;
    margin: 0 0 -25px 25px;
  }
  &__title{
    margin-top: 15px;
  }
  &__desc{
    margin-top: 47px;
  }
}

