.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: none;
    border-bottom: 1px solid rgba(102, 105, 115, 0.2);
    padding: 0;
    height: 60px;
    width: 100%;
    font-size: 24px;
    font-family: $font2;
    background-color: transparent;
    color: $black;
  }
  textarea{
    resize: none;
  }
  label{
    display: block;
    margin-bottom: 3px;
    font-size: 18px;
    line-height: 133%;
    color: $gray;
  }
  .nice-select{
    width: 100%;
    height: 51px;
    padding: 0;
    border: none;
    background-color: transparent !important;
    border-bottom: 1px solid rgba(102, 105, 115, 0.2);
    border-radius: 0;
    .current{
      padding-right: 57px;
      font-weight: 600;
      font-family: $font2;
      font-size: 18px;
      line-height: 150%;
      display: flex;
      align-items: center;
      height: 100%;
      width: 100%;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-position: right 10px center;
    }
    .list{
      margin-top: 10px;
      filter: drop-shadow(0px 4px 36px rgba(0, 0, 0, 0.1));
      border: none;
      background-color: #fff;
      box-shadow: none;
      border-radius: 0;
      width: unset;
      min-width: 100%;
    }
    .option{
      padding: 15px 30px;
      font-size: 18px;
      line-height: 150%;
      border-color: $blockBg;
      border-style: solid;
    }
  }
}

.mcheck {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    padding-left: 41px;
    position: relative;
    min-height: 24px;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      width: 20px;
      height: 20px;
      border-radius: 4px;
      background-color: #E8EAF2;
      background-repeat: no-repeat;
      background-position: center;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
    a{
      position: relative;
      z-index: 2;
    }
  }
}

.file-input{
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    border-bottom: 1px solid rgba(102, 105, 115, 0.2);
    height: 60px;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
  }
  &__name{
    width: calc(100% - 25px);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__hint{
    margin-top: 6.5px;
    font-size: 14px;
    line-height: 171%;
    color: $gray;
  }
}
