.display1{
  font-weight: 500;
  font-size: 45px;
  line-height: 131%;
  font-family: $font2;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.body-text h2,
.display2{
  font-size: 24px;
}

.display3{
  font-size: 19px;
}

.body-text h2,
.display2,
.display3{
  font-weight: 600;
  line-height: 150%;
  font-family: $font2;
}

.body-text{
  font-size: 18px;
  line-height: 133%;
  color: $gray;
  p,
  h5{
    font-size: inherit;
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 25px;
    }
    a{
      color: $primary;
    }
  }
  ul{
    line-height: inherit;
    padding-left: 12px;
    li{
      line-height: inherit;
      display: flex;
      &::before{
        content: '';
        display: block;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        background-color: $gray;
        transform: translateY(11px);
        margin-right: 12px;
        flex-shrink: 0;
      }
    }
  }
}

.black-text{
  color: $black;
}

.accent-text{
  color: $primary;
}

.linear-text{
  background: linear-gradient(93.43deg, #0D899F 6.42%, #19B8AE 44.9%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  text-shadow: 0px 0px #00000000;
}

.quote-text{
  padding-left: 26px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 6px;
    height: 100%;
    border-radius: 7px;
    background-color: $black;
  }
}


@media screen and (max-width: 1450px){
  .display1{
    font-size: 38px;
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 28px;
    line-height: 143%;
  }

  .display2{
    font-size: 20px;
    line-height: 130%;
  }

  .body-text{
    font-size: 15px;
    p{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }
}
