.modal-backdrop {
    &.show {
        opacity: 0.7;
    }
}

.modal{
    &-dialog{
        max-width: 526px;
        width: 100%;
        pointer-events: unset;
        background: #FFFFFF;
        box-shadow: 0px 4px 174px rgba(0, 0, 0, 0.25);
        padding: 75px 47px 69px;
    }
    &__close{
        position: absolute;
        right: 12px;
        top: 12px;
    }
    &__title{
        text-align: center;
    }
    &__form{
        margin-top: 69px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .fg{
            width: 100%;
            margin-bottom: 40px;
        }
    }
    &__check{
        width: 100%;
    }
    &__btn{
        padding: 0 31px;
        margin-top: 70px;
        svg{
            margin-left: 12px;
        }
    }
}
