@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-accent,
.mbtn-gray{
    height: 57px;
    border-radius: 1000px;
    font-weight: 700;
    font-size: 18px;
}

.mbtn-accent{
    background: linear-gradient(96.39deg, #0D899F -11.35%, #19B8AE 114.56%);
    color: #fff;
    &:hover{
        opacity: .8;
        color: #fff;
    }
}

.mbtn-gray{
    background-color: $pageBg;
    color: $primary2;
    &:hover{
        background-color: darken($pageBg,5%);
        color: $primary2;
    }
}


.swiper-button-prev,
.swiper-button-next{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #E3E5EF;
    &:hover{
        background: darken(#E3E5EF,10%);
    }
    &.mbtn-black{
        path{
            stroke: #fff;
        }
    }
}

.mbtn-black{
    background: $black;
    &:hover{
        background: darken($black,10%);
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-weight: 800;
    font-size: 18px;
    line-height: 161%;
    svg{
        margin-left: 19px;
    }
}

.social-btn{
    width: 52px;
    height: 52px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $bg;
    &:hover{
        background-color: darken($bg,10%);
    }
    &:not(:last-child){
        margin-right: 10px;
    }
}


@media screen and (max-width: 1200px){
    .mbtn-accent,
    .mbtn-gray{
        height: 53px;
        font-size: 15px;
    }

    .swiper-button-prev,
    .swiper-button-next{
        width: 52px;
        height: 52px;
    }
}


@media screen and (max-width: 992px){
    .arrow-link{
        font-size: 15px;
        line-height: 193%;
        svg{
            margin-left: 15px;
        }
    }
}
