@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_inputs.scss';
.tab{
    &__content{
        display: none;
        &--active{
            display: block;
        }
    }
}

.pagination{
    margin-top: 48px;
    display: flex;
    align-items: center;
    &__buttons{
        display: flex;
        align-items: center;
        margin-left: auto;
    }
    &__btn{
        margin-left: 10px;
        width: 52px;
        height: 52px;
        border-radius: 50%;
    }
    &__item{
        font-weight: 700;
        font-size: 16px;
        line-height: 188%;
        margin-right: 15px;
        &--active{
            color: $primary;
            pointer-events: none;
        }
    }
}

.video{
    position: relative;
    padding-bottom: 45%;
    video{
        pointer-events: none;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
    &--played{
        video{
            pointer-events: unset;
        }
        .video__play{
            display: none;
        }
    }
    &__play{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        z-index: 2;
    }
}

.swiper-pagination{
    z-index: 1;
}

.mob-visible{
    display: none;
}

@media screen and (max-width: 1200px){
    .num-pagination{
        width: 100%;
        min-height: 52px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        padding: 0 60px;
        .swiper-pagination-bullet{
            width: unset;
            height: unset;
            margin: 0 8px !important;
            opacity: 1;
            background-color: transparent !important;
            font-weight: 700;
            font-size: 15px;
            line-height: 150%;
            &-active{
                color: $primary;
            }
        }
    }

    .pagination{
        position: relative;
        padding: 0 60px;
        display: flex;
        justify-content: center;
        min-height: 52px;
        &__buttons{
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
        }
        &__btn{
            position: absolute;
            top: 0;
            &--prev{
                left: 0;
                margin-left: 0;
            }
            &--next{
                right: 0;
            }
        }
    }
}


@media screen and (max-width: 768px){
    .mob-visible{
        display: unset;
    }
    .desktop-visible{
        display: none;
    }
}
