.service-page{
  background-image: url(../img/wave3.svg);
  background-position: center top;
  background-repeat: no-repeat;
}

.service-main{
  padding-bottom: 65px;
  &__inner{
    display: grid;
    grid-template-columns: 470px calc(100% - 550px);
    gap: 80px;
  }
  &__right{
    position: relative;
    padding-right: 40px;
  }
  &__title{
    margin-top: 25px;
  }
  &__desc{
    max-width: 360px;
    margin-top: 85px;

  }
  &__btn{
    width: 100%;
    margin-top: 60px;
    svg{
      margin-left: 15px;
    }
  }
  .main__slider-pagination{
    position: absolute;
    height: 185px;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    flex-direction: column;
    justify-content: space-between;
    .swiper-pagination{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-61%,-50%) rotate(90deg);
    }
  }
}

.service-slider{
  max-width: 878px;
  &__item{
    position: relative;
    padding-left: 120px;
  }
  &__content{
    max-width: 634px;
    margin-left: auto;
    background: #D4EBF1;
    padding: 53px 87px 69px;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-40%);
    height: 298px;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__top{
    display: flex;
  }
  &__title{
    padding-top: 20px;
    margin-left: 15px;
  }
  &__desc{
    margin-top: 27px;
  }
  &__nav{
    padding-left: 120px;
    margin-top: 41px;
  }
  &__buttons{
    max-width: 634px;
    width: 100%;
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .swiper{
    &-button{
      &-prev,
      &-next{
        position: static;
        margin: 0 5px;
      }
    }
  }

}

.service-consult{
  padding: 60px 0;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: 49% 51%;
  }
  &__desc{
    margin-top: 40px;
  }
  &__grid{
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  &__full{
    grid-column: 1/3;
  }
  &__bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 70px;
  }
  &__btn{
    margin-right: auto;
    padding: 0 46px;
    svg{
      margin-left: 15px;
    }
  }
  &__hint{
    margin-top: 25px;
    width: 100%;
  }
  &__content{
    position: relative;
    padding-top: 142px;
  }
  &__img1{
    position: absolute;
    top: 187px;
    left: 334px;
    height: 451px;
  }
  &__img2{
    position: absolute;
    top: 550px;
    left: 140px;
    height: 296px;
    z-index: 2;
  }
  &__info{
    max-width: 416px;
    padding: 25px 30px;
    background-color: $pageBg;
    margin-left: 197px;
    position: relative;
    z-index: 3;
  }
}
